.header {
  width: 100vw;
  height: 82px;
  background: rgba(10, 23, 68, 0.7);
  box-shadow: 0px 3px 20px 0px rgba(9, 23, 70, 0.8);
  -webkit-box-shadow: 0px 3px 20px 0px rgba(9, 23, 70, 0.8);
  -moz-box-shadow: 0px 3px 20px 0px rgba(9, 23, 70, 0.8);
  opacity: 1;
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
  padding: 0 16px;
  position: fixed;
  z-index: 9999;
}

.header .headerContent {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .headerContent .headerLogo {
  width: 130px;
  height: 20px;
}

.header .headerContent .headerLogo img {
  width: 100%;
  height: 100%;
}

.header .headerContent .headerButtons {
  height: 100%;
  display: flex;
  align-items: center;
}

.header .headerContent .headerButtons button {
  width: 106px;
  height: 30px;
  border-radius: 25px;
  border: none;
  font-size: 10px;
  font-weight: bold;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .headerContent .headerButtons button img {
  width: 9px;
  height: 9px;
  margin-right: 8px;
}

@media screen and (max-width: 413px) {
  .header .headerContent .headerButtons button {
    width: 70px;
  }
}

.header .headerContent .headerButtons .buttonLight {
  margin-right: 13px;
  background: #72EFDB;
}

.header .headerContent .headerButtons .buttonDark {
  background: #1E3E7B;
  color: #ffffff;
}

@media screen and (min-width: 1024px) {
  .header {
    height: 100px;
    padding: 0 60px;
  }
